.main{
    box-sizing: border-box;
    height: 90%;
    width: 100%;
    background: #E9E9E9;
    padding: 40px;
    gap: 20px;
    display: grid;
    grid-template-areas: "a b"
                         "c c"
                         "c c";
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;
}

