.wrap {
    background: #ffffff;
    border-radius: 8px;
    width: 80%;
    padding: 30px 30px 0px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    grid-template-rows: repeat(4, 1fr);

}

.btn {
    width: 50%;
    height: 56px;
}


.submit {
    background-color: #000000;
}

